.reviews {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .review-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 9.93px;

    .review {
      position: inherit;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      background-color: $secondary-color-4;
      padding: 1rem;
      margin: 1rem;
      width: 20rem;
    }
  }
}
